.main-nav ul {
	list-style: none;

	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;

	padding: 20px 60px 0 0;

	margin: 0;
}

.main-nav li {
	margin: 0 18px 10px 18px;
}

@media (max-width: $tablet-only-width) {
	.main-nav ul {
		flex-direction: column;
		flex-wrap: nowrap;
		justify-content: flex-start;
		align-items: flex-start;

		padding-top: 46px;
	}

	.main-nav li {
		font-weight: 500;
		font-size: 24px;
		line-height: 36px;
		color: $color-bg-primary;

		margin: 0 0 21px;
	}
}

@media (max-width: $mobile-only-width) {
	.main-nav ul {
		padding-top: 36px;
	}
}